.color1 {color: #520f48;}
.color2 {color: #9c2057;}
.color3 {color: #6994d4;}
.color4 {color: #c9d9e8;}
.color5 {color: #f3f3f3;}



.login {
  display: flex;
  flex-direction: column;
  width: 90%;
  align-items: center;
  padding: 45px 5% 45px 5%;
}



.header-p {
  z-index: 9999;
  margin-bottom: 0;
}

.main-block {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  z-index: 9999;
  width: 100%;
  max-width: 450px;
  background-color: #f3f3f3;
  color: #000;
}

.input {
  width: 100%;
  height: 35px;
  margin: 0;
  padding-left: 30px;
  border: none;
  border-bottom: 1px solid #9c2057;
  transition: color .3s linear;
  background-color: #f3f3f3;
}

.button-signin {
  margin-top: 15px;
  width: 90%;
  height: 35px;
  padding: 0;
  background-color: #9c2057;
  border: none;
  color: #f3f3f3;
  transition: background-color .3s linear;
  font-weight: 600;
}

.button-signin:hover {
  background-color: #59032b;
  cursor: pointer;
}

.field-img {
  width: 15px;
  height: 15px;
  position: absolute;
  left: 35px;
}

.input-field {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
  width: 90%;
  margin-bottom: 35px;
}

.input:focus-visible {
  outline: none;
}

@media screen and (min-width: 425px) {
  .field-img {
    left: 45px;
  }



}

